import React, { Component } from 'react';
import '../../styles/header/styles.header.css';

const myColor = [
  '#BD0001',
  '#E10002',
  '#FF9904',
  '#F5EB05',
  '#3FD91E',
  '#02A645',
  '#367FE1',
  '#0058AE',
  '#08174A',
  '#630E88',
  '#0BAFC0'
];

const colorDiv = {
  display: 'flex',
  flexDirection: 'row',
  justifyContent: 'center',
  alignItems: 'center'
};

export default class Header extends Component {
  // Activate New
  activateNew() {
    console.log('HIHI');
  }

  render() {
    return (
      <div className="container">
        <div className="header">
          {/* Left Segment */}
          <div className="leftSegment">
            {/* logo */}
            <div className="logo">
              <p>Logo</p>
            </div>

            {/* newCV */}

            <div className="newCV">
              <p>ICON</p>
              <a
                href="#"
                onClick={() => this.activateNew()}
                className="menuItemText"
              >
                New
              </a>
            </div>

            {/* upload CV */}
            <div className="uploadCV">
              <p>ICON</p>
              <a
                href="#"
                onClick={() => this.activateNew()}
                className="menuItemText"
              >
                Upload
              </a>
            </div>

            {/* form CV */}

            <div className="formCV">
              <p>ICON</p>
              <a
                href="#"
                onClick={() => this.activateNew()}
                className="menuItemText"
              >
                Form
              </a>
            </div>

            {/* language */}

            <div className="language">
              <p>ICON</p>
              <a
                href="#"
                onClick={() => this.activateNew()}
                className="menuItemText"
              >
                Language
              </a>
            </div>
          </div>

          {/* Center Segment */}

          <div className="centerSegment" />

          {/* Right Segment */}

          <div className="rightSegment">
            {/* Photo */}

            <div className="photo" />

            {/*  Signature */}

            <div className="signature" />

            {/* Additional */}
            <div className="additional" />

            {/* Download */}
            <div className="download" />

            {/* Account */}
            <div className="account" />
          </div>
        </div>
        <div className="subHeader">
          {/* GUIDE SIDE */}
          <div className="guideSide">
            <div className="zoom" />
            <div className="guide" />
          </div>

          {/* COLOR PALLETE SIDE */}
          <div className="colorPalette">
            {myColor.map((color, index) => {
              return (
                <div style={colorDiv} key={index}>
                  <div
                    style={{
                      backgroundColor: color,
                      width: 22,
                      height: 22,
                      marginRight: 10,
                      borderRadius: 20,
                      borderStyle: 'solid',
                      borderWidth: 0.5,
                      borderColor: 'rgba(255, 255, 255, 0.5)'
                    }}
                  />
                </div>
              );
            })}
          </div>

          {/* ADVICE SIDE */}

          <div className="adviceSide">
            <div className="advice" />
            <div className="share" />
          </div>
        </div>
      </div>
    );
  }
}



