import React, { Component } from 'react';
import PropTypes from 'prop-types';
import '../../../styles/mainPage/cv/styles.cv.css';

const PersonalDetail = props => {
  const {
    heading,
    name,
    birthday,
    sex,
    address,
    marital,
    phone,
    email
  } = props;
  return (
    <div className="personalDetail">
      <div className="contents">
        <div className="heading">
          <h3>{heading}</h3>
        </div>

        <div className="details">
          <div className="labels">
            <p className="label name">Name</p>
            <p className="label birthday">Place and Date of Birth</p>
            <p className="label sex">Sex</p>
            <p className="label address">Address</p>
            <p className="label marital">Marital Status</p>
            <p className="label phone">Telephone Number</p>
            <p className="label phone">Email</p>
          </div>

          <div className="values">
            <p className="value name">:{name}</p>
            <p className="value birthday">:{birthday}</p>
            <p className="value sex">:{sex}</p>
            <p className="value address">:{address}</p>
            <p className="value marital">:{marital}</p>
            <p className="value phone">:{phone}</p>
            <p className="value phone">:{email}</p>
          </div>
        </div>
      </div>

      <div className="avatar">
        <img className="normal" src="/assets/images/no-avatar.png" />
      </div>
    </div>
  );
};

PersonalDetail.propTypes = {
  heading: PropTypes.string,
  name: PropTypes.string,
  birthday: PropTypes.string,
  sex: PropTypes.string,
  address: PropTypes.string,
  marital: PropTypes.string,
  phone: PropTypes.string,
  email: PropTypes.string
};

export default PersonalDetail;



